.main-title h1{
    font-size: 72px;
    font-family: 'Oswald',sans-serif;
    color: #fff;
    text-transform: uppercase;
}
.big-text{
    font-size: 300px;
    fill: transparent;
    color: transparent;
    -webkit-text-stroke: 1px #fff;
    text-transform: uppercase;
    z-index: -1;
    opacity: .2;
    position: relative;
    bottom: 80px;
    font-family: 'Oswald',sans-serif;
}
.breadcrumb-section {
    position: relative;
    z-index: 1;
    background-size: cover;
    background-position: center;
    padding-top: 245px;
   
}

.breadcrumb-shape {
    position: absolute;
    left: -30px;
    bottom: -1px;
    width: 105%;
   
    background-color: transparent;
}

.breadcrumb-section::after {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    background-color: #757a89;
    opacity: .6;
    content: '';
}
.breadcrumb-section .breadcrumb-shape svg {
    width: calc(125% + 1.3px);
    height: 125px;
    display: block;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    overflow: hidden;
}

.breadcrumb-section .breadcrumb-shape svg path {
    fill: #fff;
    -webkit-transform-origin: center;
    transform-origin: center;
    -webkit-transform: rotateY(0);
    transform: rotateY(0);
}
.toggle-btn{
    width:165px;
    height: 65px;
    background-color: #ffaa17;
    display: inline-flex;
    position: relative;
    top:30px;
    z-index: 10;
    align-items: center;
    justify-content: center;
    
}
.toggle-list{
    padding: 0;
    margin-top: 10px;
    
}
.toggle-list li{
    list-style: none;
    display: inline-flex;
    margin: 10px;
    font-size: 14px;
    font-family: 'Oswald',sans-serif;
    text-transform: uppercase;
}
.toggle-list li a{
    color: #041D38 ;

}
.toggle-list li a::after{
    content: '|';
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    color: black;
    position: relative;
    left: 10px;
}

.mask{
    width: 100%;
    height: 250px;
    z-index: 5;
    background-color: #fff;
}

